body {
  margin: 20px;
  font-family: Arial, sans-serif;
}

p {
  text-align: center;
  margin-bottom: 20px;
}

select.breed-select {
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 16px;
}

div.cat-info {
  text-align: center;
}

div.cat-info img {
  width: 50%;
  height: auto;
  margin-bottom: 10px;
}

p.error.hidden {
  display: none;
}

p.error {
  color: #c42e2e;
  text-align: center;
  font-size: 16px;
}

p.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px dotted teal;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

